export default {
    props: {
        id: {
            type: String,
            default: _ => '',
        },
    },
    data() {
        return {
            roles: [
                {
                    name: 'head',
                    title: this.isENG
                        ? 'Head of the Laboratory'
                        : 'Руководитель Лаборатории',
                    imgW: 275,
                },
                {
                    name: 'deputy',
                    title: this.isENG
                        ? 'Leaders of the research directions'
                        : 'Руководители научных направлений',
                    imgW: 250,
                },
                {
                    name: 'researcher',
                    title: this.isENG
                        ? 'Researchers'
                        : 'Научные сотрудники',
                    imgW: 225,
                },
                {
                    name: 'phdstudent',
                    title: this.isENG
                        ? 'Ph.D. students'
                        : 'Аспиранты',
                    imgW: 200,
                },
                {
                    name: 'student',
                    title: this.isENG
                        ? 'Students'
                        : 'Студенты',
                    imgW: 175,
                }
            ],
        }
    },
    computed: {
        person_() {
            if(this.id)
                return this.teamTbl.row(this.id)
        },
        person_Head() {
            return this.teamTbl.rows.find(m => m.surname == 'Cichocki')
        },
        team() {
            return this.teamTbl.rows.filter(p => p.active)
        },
        ids() {
            return this.team.map(p => p.id)
        },
        titles() {
            return this.team
                .map(p => this.isENG
                    ? p.name+' '+p.surname
                    : p.name_rus+' '+p.surname_rus)
        },
        rolesSub() {
            return
        },
    },
    methods: {
        select(id=undefined) {
            this.$router.push({name: 'dt-page-team', params: {id: id}})
        },





        sort_() {
            return
            this.teamTbl.rows.sort((p1, p2) => {
                if(p1.order < p2.order)
                    return -1
                return 1
            })
            //this[tblName]
        },
        personImg(person, isThumb=false) {
            let img = `static/pictures/dt/team/team`
            img+= `_${person.surname.toLowerCase()}_${person.name.toLowerCase()}`
            img+= isThumb ? `_thumb` : ''
            img+= `.jpg`
            return img
        },

    }
}



