<script>

import TeamMixin from './_TeamMixin'
import Slider from './../Slider'
import Person from './Person'

export default {
    name: 'dt-team',
    mixins: [TeamMixin],
    components: {
        'dt-slider': Slider,
        'dt-person': Person,
    },
}
</script>

<template lang="pug">

div.dt-team
