        :titles="titles"
        :ids="ids"
        :id="id"
        @select="id_ => select(id_)")
    dt-person.dt-team__person(
        :person="person_")
</template>

<style lang="sass">

@import '~@/sass/root.sass'

.dt-team
    &__progress
        position: absolute
        left: 0
        top: 0
        padding: 0
        margin: 0
    &__title
        +margin(50px 0 40px 30px)
        &-img
            text-align: center
    &__aside
        +margin(20px)
.duplex
    +flex(row, wrap, space-around, center)
    &_reverse
        flex-wrap: wrap-reverse
    &__item
        flex: 1 0 300px
        +flex-basis($micro: 250px)
        +margin(20px)
        &_main
            flex: 2 0 60%
        &_small
            flex: 0 1 300px
        &_top
            align-self: flex-start
.gallery
    +flex(row, wrap, space-around, flex-start)
    padding: 5px
    &__item
        +margin(30px)
    &_small
        flex: 0 1 200px

.slider
    +flex(row, nowrap, space-between)
    +padding(20px)
    height: auto
    margin: 20px auto
    &__dots
        +flex(row, wrap, space-around, center, space-between)
        +padding(10px)
    &__button
        cursor: pointer
        &_arrow-left
        &_arrow-right
        &_close
        &_dot
            box-sizing: border-box
            width: 10px
            height: 10px
            margin: 10px
            border: 7px solid rgb(107, 126, 81)
            border-radius: 50%
            &:hover
                border: 7px solid rgb(0, 58, 86)
            &_active
                border: 7px solid rgb(0, 58, 86)
                cursor: default
                &:hover
                    border: 7px solid rgb(0, 58, 86)
</style>



