<script>

import TeamList from '@dt/components/team/TeamList'
import Team from '@dt/components/team/Team'

export default {
    name: 'dt-page-team',
    components: {
        'dt-team-list': TeamList,
        'dt-team': Team,
    },
    props: {
        id: {
            type: String,
            default: '',
        },
    },
}
</script>

<template lang="pug">

div.dt-page-team
    dt-team-list(
        v-if="!id")
    dt-team(
        v-if="id"
        :id="id")
</template>



