<script>

import TeamMixin from './_TeamMixin'
import PersonThumb from './PersonThumb'

export default {
    name: 'dt-team-list',
    mixins: [TeamMixin],
    components: {
        'dt-person-thumb': PersonThumb,
    },
    data() {
        return {
            textCommand: {
                en:
                    'The Laboratory brings together several professors and young researchers in the fields of machine learning, computer vision, artificial intelligence, robotics, large scale data analysis, mathematics as well as computational neuroscience and bioinformatics.',
                ru:
                    'Наш коллектив объединяет профессоров и молодых исследователей, являющихся специалистами мирового уровня в области машинного обучения, компьютерного зрения, искусственного интеллекта, робототехники, анализа  больших объемов данных, а также вычислительной математики, нейробиологии и биоинформатики.',
            },
            textOpenPos: {
                en:
                    'We opened several research positions for various levels including assistant professors, postdoc and  PhD students. The Laboratory  offers a unique research program and also PhD fellowship program.',
                ru:
                    'В Лаборатории есть открытые позиции для научных сотрудников, аспирантов и студентов.',
            },
        }
    },
}
</script>

<template lang="pug">

div.dt-team-list
    dkbr-progress.dt-team-list__progress(
        v-if="!teamTbl.isLoaded || !teamTbl.isParsed"
        :isInf="true")
    h2.dt-team-list__title {{ roles[0].title }}
    section.dt-team-list__head
        div.dt-team-list__head-image
            dt-person-thumb(
                :person="person_Head"
                @select="select(person_Head.id)")
        aside.dt-team-list__head-text
            p.dt-text(
                v-html="textCommand[LANG]")
            p.dt-text(
                v-html="textOpenPos[LANG]")
            router-link.dt-em.dt-em_spec.dt-em_right.link(
                style="text-align: right;"
                tag="a",
                :to=`{name: 'dt-page-events', params: {id: 'recruitment'}}`
                ) {{ isENG ? 'More info' : 'Подробнее' }}
    section(
        v-if="role.name != 'head'"
        v-for="role of roles"
        :key="role.name")
        h2.dt-team-list__title {{ role.title }}
        div.dt-team-list__gallery
            dt-person-thumb.dt-team-list__gallery-item(
                v-if="person.role == role.name"
                v-for="person of team"
                :key="person.id"
                :person="person"
                @select="select(person.id)")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-team-list
    &__progress
        position: absolute
        padding: 0
        margin: 0
    &__head
        +flex(row, wrap, space-around, center)
        &-image
            +width(300px)
        &-text
            flex: 1 0 200px
            align-self: flex-start
            +margin(20px)
    &__title
        +display(inline-block)
        +margin(50px 0 40px 30px)
        +padding(20px 50px 20px 100px, $micro: 15px 30px 15px 80px, $nano: 10px 20px 10px 50px)
        +color(light1, dark3)
        +font(base-bold-huge)
        +shadow(box3d-dark)
    &__gallery
        +flex(row, wrap, space-around, flex-start)
        padding: 5px
        &-item
            +margin(30px)
</style>



