<script>

export default {
    name: 'dt-slider',
    props: {
        titles: {
            type: Array,
            default: _ => [],
        },
        ids: {
            type: Array,
            default: _ => [],
        },
        id: {
            type: String,
            default: _ => '',
        },
    },
    data() {
        return {
            textClose: this.isENG ? 'Close' : 'Закрыть',
            textPrev: this.isENG ? 'Previous' : 'К предыдущему',
            textNext: this.isENG ?  'Next' : 'К следующему',
        }
    },
    computed: {
        prev() {
            const i = this.ids.findIndex(id => id == this.id)
            return this.ids[i-1]
        },
        next() {
            const i = this.ids.findIndex(id => id == this.id)
            return this.ids[i+1]
        },
    },
}
</script>

<template lang="pug">

div.dt-slider
    dkbr-button.dt-slider__button-left(
        :icon="prev ? 'keyboard_arrow_left' : 'close'"
        :iconSize="2"
        @click.native="$emit('select', prev)")
    div.dt-slider__dots
        div.dt-slider__dots-dot(
            v-for="(id_, i) of ids"
            :key="id_"
            :class=`{'dt-slider__dots-dot_active': id_ == id}`
            @click="id_ != id ? $emit('select', id_) : undefined")
            dkbr-tooltip(
                v-if="titles[i]"
                dir="bottom"
                :text="titles[i]")
    dkbr-button.dt-slider__button-right(
        :icon="next ? 'keyboard_arrow_right' : 'close'"
        :iconSize="2"
        @click.native="$emit('select', next)")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-slider
    width: 100%
    +flex(row, nowrap, space-between, center)
    &__dots
        +flex(row, wrap, space-around, center, space-between)
        +padding(10px)
        &-dot
            box-sizing: border-box
            width: 10px
            height: 10px
            margin: 10px
            border: 7px solid rgb(107, 126, 81)
            border-radius: 50%
            cursor: pointer
            &:hover
                border: 7px solid rgb(0, 58, 86)
            &_active
                border: 7px solid rgb(0, 58, 86)
                cursor: default
                &:hover
                    border: 7px solid rgb(0, 58, 86)
    &__button
        cursor: pointer
</style>



