export default {
    props: {
        person: {
            type: Object,
            default: {},
        },
    },
    computed: {
        name() {
            return this.get_('name')
        },
        surname() {
            return this.get_('surname')
        },
        img() {
            if(!this.person || !this.person.surname)
                return ''
            let img = `/static/pictures/dt/team/team`
            img+= `_${this.person.surname.toLowerCase()}`
            img+= `_${this.person.name.toLowerCase()}`
            img+= `.jpg`
            return img
        },
        imgThumb() {
            return this.img ? this.img.replace(`.jpg`, `_thumb.jpg`) : ''
        },
        cv() {
            if(!this.person || !this.person.surname)
                return ''
            let s = this.person.surname
            let n = this.person.name
            return `/static/pdf/dt/pdf/CV_${s}_${n}.pdf`
        },
    },
    methods: {
        get_(opt) {
            if(this.isRUS && this.person[opt + '_rus'] !== undefined)
                return this.person[opt + '_rus']
            return this.person[opt]
        },
        toggleShowMail() {
            this.isShowMail = !this.isShowMail
        },
        pubToggle(year) {
            if(this.pubYearCurrent != year)
                this.pubYearCurrent = year
            else
                this.pubYearCurrent = undefined
        },
    },
}



