<script>

import PersonMixin from './_PersonMixin'

export default {
    name: 'dt-person-thumb',
    mixins: [PersonMixin],
    props: {
        isHead: {
            type: Boolean,
            default: false,
        }
    },
    data() {
        return {
            widths: {
                head : 275,
                deputy: 250,
                researcher: 225,
                phdstudent: 200,
                student: 175,
            },
        }
    },
    computed: {
        caption() {
            if(!this.isHead)
                return this.name + '<br>' + this.surname
            if(this.isENG)
                return 'Doctor Andrzej Cichocki<br>Head of the Laboratory'
            if(this.isRUS)
                return 'Доктор ф.-м. наук<br>Анджей Чихоцкий<br>Руководитель Лаборатории'
        },
    },
}
</script>

<template lang="pug">

div.dt-person-thumb(
    @click="$emit('select')")
    dkbr-figure.dt-person-thumb__image(
        :src="imgThumb"
        :ws="widths[person.role]")
        p.dt-person-thumb__text(
            v-html="caption")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-person-thumb
    +padding(10px)
    +border(light)
    +color-font(dark3)
    +font(base-bold-huge)
    text-decoration: underline
    text-align: center
    cursor: pointer
    &:hover
        +border(dark)
        +shadow(box3d-dark)
        +color-font(light3)
        text-decoration: none
    &__image
    &__text
        +padding(5px 5px 0)
</style>



